<template>
  <vue-draggable-resizable
      :on-resize="onResize"
      ref="sqlResizable"
      :class="`resizable sql-logs ${isSqlLogsHidden && 'display-none'}`"
      :draggable="false"
      :resizable="true"
      :w="width"
      :h="200"
      :y="0"
      :min-height="200"
      :max-height="500"
      :handles="['tm']"
      axis="y"
  >
    <div class="sql-logs-pre" ref="sqlLogsPre">
      <ssh-pre language="sql" :data-list="queries" :reactive="true"/>
    </div>
  </vue-draggable-resizable>
</template>

<script>

import Socket from '@/api/Socket'
import timeAgo from "@/utils/timeAgo";
import SshPre from '@/components/simple-syntax-highlighter'

export default {

  components: {SshPre},

  data() {
    return {
      width: 100,
      queries: []
    }
  },

  mounted() {
    this.width = this.$el.getBoundingClientRect().width
    Socket.onUnpackedMessage.addListener(this.onMessage)
  },

  computed: {
    isSqlLogsHidden() {
      const isHidden = this.$store.state.isSqlLogsHidden
      if (!isHidden) this.scrollToBottom()
      return isHidden
    }
  },

  beforeDestroy() {
    Socket.onUnpackedMessage.removeListener(this.onMessage)
  },

  methods: {
    onResize(_, x, y, width, height) {
      this.$store.commit('updateSqlLogsHeight', height)
    },

    onMessage(message) {
      if (message.type === "sql") {
        this.queries.push(`-- ${timeAgo(new Date())}`)
        this.queries.push(message.data.sql)
        this.scrollToBottom()
      }
    },

    scrollToBottom() {
      this.$nextTick(() => {
        this.$refs.sqlLogsPre.scrollTo(0, document.body.scrollHeight)
      })
    }
  }
}
</script>