<template>
  <div class="container-xl">
    <div class="Header">
      <div class="Header-item">
        <a href="#" @click.prevent="$store.commit('toggleTablesSideBarHidden')" class="Header-link">
          <span v-if="!isTablesSideBarHidden">Hide Tables</span>
          <span v-else>Show Tables</span>
        </a>
      </div>
      <div class="Header-item">
        <a href="#" @click.prevent="$store.commit('toggleSqlLogsHidden')" class="Header-link">
          <span v-if="!isSqlLogsHidden">Hide Logs</span>
          <span v-else>Show Logs</span>
        </a>
      </div>
    </div>
    <div style="width: 100%; height: calc(100% - 53px)">
      <TablesSideBar/>

      <div class="nestedRouteViewer float-left"
           :style="{width: `calc(100% - ${tablesSideBarWidth}px)`, height: '100%', position: 'relative'}">
        <div class="table-container" :style="{height: `calc(100% - ${sqlLogsHeight}px)`}">
          <router-view :key="$route.path"></router-view>
          <div class="empty-page" v-if="$route.fullPath === `/database/${$route.params.database}`">
            <h1>Choose table</h1>
          </div>
        </div>
        <SqlLogs :style="{width: `100%`}"/>
      </div>
    </div>
  </div>
</template>

<script>
import Socket from "@/api/Socket";
import SqlLogs from "@/components/SqlLogs";
import TablesSideBar from "@/components/TablesSideBar";

export default {
  components: {SqlLogs, TablesSideBar},

  data() {
    return {
      key: this.$route.path
    }
  },

  async mounted() {
    const tables = await Socket.sendRequest({type: 'tables', databaseName: this.$route.params.database})
    this.$store.commit('setTables', tables.data)
  },

  computed: {
    sqlLogsHeight() {
      return this.$store.state.sqlLogsHeight
    },
    tablesSideBarWidth() {
      return this.$store.state.tablesSidebarWidth
    },

    isTablesSideBarHidden() {
      return this.$store.state.isTablesSideBarHidden
    },

    isSqlLogsHidden() {
      return this.$store.state.isSqlLogsHidden
    },
  },
}
</script>

