<template>
  <vue-draggable-resizable
      :onResize="onResize"
      :class="`SideNav border col-3 float-left resizable ${isTablesSideBarHidden && 'display-none'}`"
      ref="leftSideNav"
      :draggable="false"
      :resizable="true"
      :w="tablesSidebarWidth"
      :min-width="250"
      :max-width="500"
      :handles="['mr']"
      axis="x"
  >
    <div class="side-nav-resize-hand"/>
    <div class="items" style="height: 100%;overflow:scroll;">
      <router-link
          :to="{
                  name: 'table',
                  params: {
                    table: table.tableName,
                  },
                  query: {
                    order: '1',
                    type: 'asc',
                  }
                }"
          v-for="table in $store.state.tables"
          :key="table.tableName"
          class="SideNav-item">
        {{ table.tableName }}
      </router-link>
    </div>
  </vue-draggable-resizable>
</template>

<script>
export default {

  computed: {
    isTablesSideBarHidden() {
      return this.$store.state.isTablesSideBarHidden
    },

    tablesSidebarWidth() {
      return this.$store.state.tablesSidebarWidth
    }
  },

  methods: {
    onResize(_, x, y, width, height) {
      this.$store.commit('updateTablesSidebarWidth', width)
    }
  }
}
</script>